import { BabylonLoader, Config, Game } from './game';
import { Editor } from './lib';

export * from './index';

let game!: Game;
let initFlag: boolean = false;
let dataLoaded: boolean = false;
let dataLoaded2: boolean = false;

// let path1: string = '';
// let path2: string = '';
let datas: any;

// 
window.editor = new Editor();
