    public static rgb2hsv(rgb: number[]): number[] {
        var rr, gg, bb,
            r = rgb[0] / 255,
            g = rgb[1] / 255,
            b = rgb[2] / 255,
            h = 0, s,
            v = Math.max(r, g, b),
            diff = v - Math.min(r, g, b),
            diffc = function (c: number) {
                return (v - c) / 6 / diff + 1 / 2;
            };

        if (diff == 0) {
            h = s = 0;
        } else {
            s = diff / v;
            rr = diffc(r);
            gg = diffc(g);
            bb = diffc(b);

            if (r === v) {
                h = bb - gg;
            } else if (g === v) {
                h = (1 / 3) + rr - bb;
            } else if (b === v) {
                h = (2 / 3) + gg - rr;
            }
            if (h < 0) {
                h += 1;
            } else if (h > 1) {
                h -= 1;
            }
        }
        return [h, s, v];
    }

    public static hsv2rgb(hsv: number[]): number[] {
        var h = hsv[0];
        var s = hsv[1];
        var v = hsv[2];
        var r = 0, g = 0, b = 0, i, f, p, q, t;
        // if (h && s === undefined && v === undefined) {
        //     s = h.s, v = h.v, h = h.h;
        // }
        i = Math.floor(h * 6);
        f = h * 6 - i;
        p = v * (1 - s);
        q = v * (1 - f * s);
        t = v * (1 - (1 - f) * s);
        switch (i % 6) {
            case 0: r = v, g = t, b = p; break;
            case 1: r = q, g = v, b = p; break;
            case 2: r = p, g = v, b = t; break;
            case 3: r = p, g = q, b = v; break;
            case 4: r = t, g = p, b = v; break;
            case 5: r = v, g = p, b = q; break;
        }
        return [Math.round(r * 255), Math.round(g * 255), Math.round(b * 255)];
    }

    public static rgba2hsvString(rgba: number[]): string {
        rgba = Tools.to255rgba(rgba);
        let colorStr = '#';
        for (let i = 0, len = rgba.length; i < len; i++) {
            let hex = ('0' + rgba[i].toString(16)).slice(-2);
            colorStr += hex;
        }
        return colorStr;
    }

