    public static hsvString2rgba(hsv: string): number[] {
        var colorChange = [];
        for (var i = 1; i < hsv.length; i += 2) {
            colorChange.push(parseInt('0x' + hsv.slice(i, i + 2)));
        }
        colorChange = Tools.toSmallrgba(colorChange);
        return colorChange;
    }

    public static to255rgba(rgba: number[]): number[] {
        let rgba255: number[] = [];
        rgba.forEach((element: number) => {
            rgba255.push(element * 255);
        });
        return rgba255;
    }

    public static toSmallrgba(rgba: number[]): number[] {
        let rgbaSmall: number[] = [];
        rgba.forEach((element: number) => {
            rgbaSmall.push(element / 255.0);
        });
        return rgbaSmall;
    }



